@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900);
@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900);
.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  font-family: "Poppins";
  font-size: 22px;
}

table,
th,
td {
  border: 1px solid black;
  padding: 15px;
  border-collapse: collapse;
}

.formControl {
  margin-left: 0px;
  margin-top: 0px;
  width: 526px;
}

.sec_1 {
  color: rgb(255, 253, 253);
  background-color: rgb(22, 0, 122);
  padding: 1px;
  text-align: center;
  border-radius: 20px;
  margin: 50px;
}
.sec_2 {
  text-align: center;
  padding: 10px;
  margin: 50px;
  border: 2px solid black;
  border-radius: 20px;
}
.formGroup {
  padding: 0px;
  margin: 50px;
  border: 2px solid black;
  border-radius: 20px;
}
.btn btn-primary {
  padding: 10px;
}
#add {
  padding: 5px;
  margin-left: 50px;
}
#clear {
  padding: 5px;
  margin-left: 20px;
}
#items {
  border: 1px solid black;
  text-align: center;
  padding: 10px;
  border-radius: 10px;
  margin: 50px;
  border-width: 4px;
}
#description {
  margin: 30px;
  width: 50%;
  margin-bottom: 20px;
}
#descr {
  margin: 30px;
  text-align: left;
  margin-bottom: 0px;
}
#titl {
  margin: 30px;
  margin-bottom: 0px;
}
#title {
  margin: 30px;
  margin-bottom: 0px;
}
#et {
  margin: 30px;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  background: #eee;
  color: #666;
}
.login-container {
  height: 100vh;
  width: 100%;
}
.login-form {
  margin: auto;
  width: 370px;
  padding: 15px;
  max-width: 100%;
}
.login-form .form-control {
  font-size: 15px;
  min-height: 48px;
  font-weight: 500;
}
.login-form a {
  text-decoration: none;
  color: #666;
}
.login-form a:hover {
  color: #723dbe;
}
.forgot-link {
  font-size: 13px;
}

.form-control:focus {
  border-color: #723dbe;
  box-shadow: 0 0 0 0.2rem rgba(114, 61, 190, 0.25);
}
.btn-custom {
  background: #723dbe;
  border-color: #723dbe;
  color: #fff;
  font-size: 15px;
  font-weight: 600;
  min-height: 48px;
}
.btn-custom:focus,
.btn-custom:hover,
.btn-custom:active,
.btn-custom:active:focus {
  background: #54229d;
  border-color: #54229d;
  color: #fff;
}
.btn-custom:focus {
  box-shadow: 0 0 0 0.2rem rgba(114, 61, 190, 0.25);
}

